import React from 'react'
// import PropTypes from 'prop-types'

import { Flex } from 'rebass'

// ----------------------------------------------------------------------------

export const Split = ({ children, ...props }) => (
  <Flex {...props} justifyContent="space-between">
    {children}
  </Flex>
)

Split.propTypes = {}

export default Split

